.App-header{
  display:flex;  background:#090909;  color:#fff;
  font-size:0.6rem;   padding-left:20px; 
}
div{
  box-sizing: border-box;
}
.list{
  padding:16px 20px;
  border-bottom:1px dashed #ccc;
  overflow:hidden;
  color:#5d5d5d;
}
.list button{
  float:right;
}
h3{
  color:#333;  cursor:pointer;
}
h3 span{
  font-size:0.5rem;  font-weight: normal;  color:#f60; 
}
h3 span:first-child{
  cursor:pointer; 
}
.write-box{
  display:flex;  justify-content:center;  margin:16px 0;
}
.write-box input{
  padding:4px 8px;  border:1px solid #ccc;  height:20px;
}
.write-box button{
  height:30px;  border:0px;  background:#090909;  color:#fff;
  border-radius:4px;  margin-left:8px;
}
.detail{
  background:#f4f4f4;  box-shadow:0px 3px 5px 0px rgba(137, 93, 239, 0.3);
  margin:30px;  padding:20px;  border-radius:10px;
}
.detail h4{
  border-bottom:2px solid #5d5d5d;  padding-bottom:5px;
}

